<template>
  <div class="game-container">
    <h1 class="game-title">LEAON CARD GAME</h1>
    <div class="game-board">
      <div
        class="card"
        v-for="(card, index) in cards"
        :key="index"
        @click="flipCard(index)"
        :class="{ flipped: card.isFlipped || card.isMatched }"
      >
        <div class="card-inner">
          <div class="card-front"></div>
          <div class="card-back">
            <img :src="card.image" alt="Card Image" />
          </div>
        </div>
      </div>
    </div>
    <div class="game-footer">
      <p>Moves: {{ moves }}</p>
      <button @click="resetGame">Restart Game</button><br>
      <button class="next-level-button">Next Level</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      images: [
        require('@/assets/game1.jpg'),
        require('@/assets/game2.jpg'),
        require('@/assets/game3.jpg'),
      ],
      cards: [],
      flippedCards: [],
      moves: 0,
    };
  },
  methods: {
    shuffle(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array;
    },
    initializeGame() {
      const doubledImages = [...this.images, ...this.images];
      this.cards = this.shuffle(
        doubledImages.map((image) => ({ image, isFlipped: false, isMatched: false }))
      );
      this.flippedCards = [];
      this.moves = 0;
    },
    flipCard(index) {
      const card = this.cards[index];
      if (card.isFlipped || card.isMatched || this.flippedCards.length === 2) {
        return;
      }

      card.isFlipped = true;
      this.flippedCards.push(index);

      if (this.flippedCards.length === 2) {
        this.moves++;
        const [firstIndex, secondIndex] = this.flippedCards;
        const firstCard = this.cards[firstIndex];
        const secondCard = this.cards[secondIndex];

        if (firstCard.image === secondCard.image) {
          firstCard.isMatched = true;
          secondCard.isMatched = true;
          this.flippedCards = [];
        } else {
          setTimeout(() => {
            firstCard.isFlipped = false;
            secondCard.isFlipped = false;
            this.flippedCards = [];
          }, 1000);
        }
      }
    },
    resetGame() {
      this.initializeGame();
    },
  },
  mounted() {
    this.initializeGame();
  },
};
</script>

